*{
    box-sizing: border-box;
    font-family: 'Big Shoulders Text', cursive;
    font-family: 'Source Sans Pro', sans-serif;
}

body{
    background-color: beige;
    margin: 0;
    padding: 0;
    height: 100%;
}

html,
body,
 #root,
.container{
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.movies{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 80%;
}

.movies .movie {
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    color: #d2d2e2;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,.25), 0 8px 16px -8px rgba(0,0,0,.3), 0 -6px 16px -6px rgba(0,0,0,.025);
    border-radius: 0.5px;
}

.movie img{
    position: relative;
    max-width: 150px;
    padding: 0;
    width: 100%;
    top: -50px;
    box-shadow: 0 30px 60px -12px rgba(50,50,93,.25), 0 18px 36px -18px rgba(0,0,0,.3), 0 -12px 36px -8px rgba(0,0,0,.025);;
}

.movie .movie__title,
.movie .movie__year{
    font-weight: 300;
    margin: 0;
}

.movie .movie__title{
    margin-bottom: 5px;
    font-size: 25px;
    color: #3c3c3c;
}

.movie .movie__genres{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    padding: 5px 0px;
}

.movie__genres li{
    margin-right: 5px;
    font-size: 20px;
}

.movie__data{
    padding-left: 30px;
}